<template>
  <!-- <div class="card card-custom card-stretch gutter-b"> -->
  <div class="card-body pt-0 pb-3 mt-10">
    <div class="tab-content">
      <!--begin::Table-->
      <template v-for="(dataset, dataset_index) in SOTA_infos">
        <div
          v-bind:key="'dataset_decs' + dataset_index"
          class="card-header border-0 py-5"
        >
          <h3 class="card-title align-items-start flex-column">
            <span class="card-label font-weight-bolder text-dark h2"
              >{{ dataset.name }}<br
            /></span>

            <p
              class="pt-3 font-weight-bold font-size-lg"
              style="line-height: 1.5"
            >
              {{ dataset.description }}
            </p>
          </h3>
          <div class="table-responsive">
            <!-- <b-table
              class="table table-head-custom table-vertical-center table-head-bg table-borderless"
              :items="SOTA_infos"
            >

            </b-table> -->
            <table
              class="table table-head-custom table-vertical-center table-head-bg table-borderless"
              v-bind:key="'dataset_name' + dataset_index"
              :title="dataset.name"
            >
              <thead>
                <tr>
                  <th style="min-width: 250px" class="pl-7">
                    <span class="text-dark-75">Model</span>
                  </th>
                  <th style="min-width: 100px">Paper</th>
                  <th style="min-width: 100px">GitHub</th>
                  <th
                    v-bind:key="dataset.name + metric_key + metric_value"
                    style="min-width: 100px"
                    class="text-center"
                    v-for="(metric_value, metric_key) in dataset.models[0]
                      .metric"
                    data-sortable="true"
                  >
                    {{ metric_key }}
                  </th>
                </tr>
              </thead>
              <tbody>
                <template v-for="(sota, sota_index) in dataset.models">
                  <!-- start::model_info-->
                  <tr v-bind:key="'model_info' + sota_index">
                    <td class="pl-2 py-8">
                      <div class="d-flex align-items-center">
                        <div class="symbol symbol-50 symbol-light mr-4"></div>
                        <div>
                          <p
                            class="text-dark-75 font-weight-bolder mb-1 font-size-lg"
                          >
                            {{ sota.model_name.toUpperCase() }}
                          </p>
                          <span class="text-muted font-weight-bold d-block">{{
                            sota.paper_name
                          }}</span>
                        </div>
                      </div>
                    </td>
                    <!-- end::model_info-->

                    <!-- start::paper_link-->
                    <td class="pl-0 py-8 pl-2">
                      <div class="d-flex align-items-center">
                        <div class="symbol symbol-50 symbol-light mr-4"></div>
                        <div>
                          <a
                            class="mb-1"
                            target="_blank"
                            :href="sota.paper_link"
                          >
                            <span class="svg-icon svg-icon-xl">
                              <inline-svg src="media/logos/paper.svg" />
                            </span>
                          </a>
                        </div>
                      </div>
                    </td>
                    <!-- end::paper_link-->

                    <!-- start::github_link-->
                    <td class="pl-1 py-8">
                      <div class="d-flex align-items-center pl-1">
                        <div class="symbol symbol-50 symbol-light mr-4"></div>
                        <div>
                          <a
                            class="text-dark-75 font-weight-bolder text-hover-primary mb-1 font-size-lg"
                            target="_blank"
                            :href="sota.github_link"
                          >
                            <span class="svg-icon svg-icon-xl">
                              <inline-svg src="media/logos/github-1.svg" />
                            </span>
                          </a>
                        </div>
                      </div>
                    </td>
                    <!-- end::github_link -->

                    <!-- start::metrics-->
                    <template v-for="(metric_value, metric_key) in sota.metric">
                      <td
                        v-bind:key="'score_' + metric_key"
                        class="pl-5 py-8 text-center"
                      >
                        <p
                          class="text-dark-75 text-center font-weight-bolder mb-1 font-size-lg"
                        >
                          {{ metric_value }}
                        </p>
                      </td>
                    </template>
                  </tr>
                </template>
              </tbody>
            </table>
          </div>
        </div>
      </template>

      <!--end::Table-->
    </div>
  </div>
  <!--end::Body-->
  <!-- </div> -->
</template>

<script>
export default {
  name: "widget-dataset",
  props: ["SOTA_infos"],
  data() {
    return {};
  },
};
</script>
